.section {
  height: 100vh;

  position: relative;
  z-index: 2;
}

.active {
  transform: translateY(-100vh);
  transition: 0.9s transform ease;
}

.inactiveUnder {
  transform: translateY(-100vh);
  transition-delay: 0.9s;
  opacity: 0;
}

.inactive {
  transform: translateY(0);
  transition: 0.9s all ease;
}

.img {
  z-index: -1;
  width: 100%;
  height: 100vh;
}

.img_block {
  position: relative;
}

.button {
  content: "";
  position: absolute;
  width: 137px;
  border-radius: 120px;
  height: 137px;
  bottom: -75px;
  z-index: 1;
  left: 0;
  right: 0;
  background: #000000;
  opacity: 0.67;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.arrow {
  margin-bottom: 65px;
}